import "./Cart.css";
import { Container, Row, Col } from "react-bootstrap";
export default function Cart({ cartItem }) {
  console.log(`i am from cart ${cartItem}`);
  const itemValues = cartItem[0];
  console.log(`obect value: ${itemValues}`);
  console.log(typeof cartItem);
  let itemIdArray = [];
  const cartItemArray = cartItem.map((newItem) => {
    const itemId = newItem.id;
    let value = 0;
    for (var i = 0; i < itemIdArray.length; i++) {
      if (itemIdArray[i] === itemId) {
        value++;
      } else {
        itemIdArray.push(itemId);
      }
    }

    return (
      <div className="mainCard">
        <Row className="itemcard" key={itemValues.id}>
          <Col sm={4}>
            <img src={newItem.url} className="imgs" alt={newItem.type} />
          </Col>
          <Col sm={8}>
            <div className="itemdes">
              <h3>Product Name{newItem.name}</h3>
              <h4>Product type:{newItem.type}</h4>
              <h4>Price: {newItem.price}$</h4>
            </div>
          </Col>
        </Row>
      </div>
    );
  });
  return (
    <div>
      <h1>Items in your cart</h1>
      <Container>{cartItemArray}</Container>
    </div>
  );
}
